import React from "react";
import "./Singo.css";
import HomeIcon from "@material-ui/icons/Home";

class Singo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      title: false,
      content: false,
    };
  }

  home = (e) => {
    window.location.replace("/main");
  };

  onChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value, // input    
    }); //     state   
  };

  onSubmit = (e) => {
    //      
    e.preventDefault();
    const data = {
      title: this.state.title,
      content: this.state.content, //  id state data.id 
    };

    let length = this.state.content;

    if (this.state.title.trim() === "" || this.state.content.trim() === "") {
      alert("   ");
    } else if (length.length >= 200) {
      alert("200  ");
    } else {
      fetch("api/Singo", {
        method: "post",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data), // json 
      });

      //   this.setState({
      //     title: "",
      //     content: "",
      //   });

      alert("  ");
    }
  };

  render() {
    return (
      <div className="Singo_page">
        <header></header>
        <body className="Singo_body">
          <span>
            {" "}
               WESIX 
            <br />      <br />
              
          </span>
          <article>
            <div className="Singo_article">
              <form>
                <div className="first_input">
                  <span></span>
                  <input
                    type="text"
                    name="title"
                    placeholder=""
                    onChange={this.onChange}
                  ></input>
                </div>

                <div className="second_input">
                  <div></div>
                  <div>
                    <textarea
                      name="content"
                      placeholder=" 200 "
                      onChange={this.onChange}
                    ></textarea>
                  </div>
                </div>
              </form>
            </div>
          </article>
        </body>
        <footer>
          <button onClick={this.onSubmit}></button>

          <HomeIcon
            className="Home_footer"
            onClick={this.home}
            style={{ fontSize: 50 }}
          />
        </footer>
      </div>
    );
  }
}

export default Singo;
