import React, { Component } from "react";
import "./Update.css";

export default class Update extends Component {
  constructor(props) {
    super(props);
    const user_info = JSON.parse(localStorage.getItem("user"));

    this.state = {
      _id: user_info.user_id,
      pass: "",
      pass2: "",
      email: "",
      nickname: user_info.user_nickname,
      update_nickname: "",
      gender: "",
    };
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };
  //   

  check = (re, what, message) => {
    if (re.test(what)) {
      return true;
    }
    alert(message);
    return false;
  };

  chageNickname = () => {
    var re = /^[a-zA-z-0-9]{2,8}$/;
    if (
      !this.check(
        re,
        this.state.update_nickname,
        " 2~8    ."
      )
    ) {
      return false;
    } else {
      const upNickname = {
        preNick: this.state.nickname,
        nick: this.state.update_nickname,
      };

      fetch("api/Update_nick", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(upNickname),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json) {
            const update_info = JSON.parse(localStorage.getItem("user"));
            update_info.user_nickname = this.state.update_nickname;
            localStorage.setItem("user", JSON.stringify(update_info));

            this.setState({
              user_nickname: JSON.parse(localStorage.getItem("user"))
                .user_nickname,
            });
            alert(" .");
          } else {
            alert("   .");
          }
        });
    }
  };
  onclickss = (e) => {
    e.preventDefault();
    window.location.replace("/main");
  };
  updatePassword = () => {
    if (this.state.pass === "") {
      alert("  ");
    } else if (this.state.pass === this.state.pass2) {
      const password = {
        _id: this.state._id,
        pass: this.state.pass,
      };
      fetch("api/Update_password", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(password),
      }).then(alert(" ."));
    } else {
      alert("  .");
    }
  };

  render() {
    return (
      <div className="White_sign">
        <form className="Container_sign" onSubmit={this.onSubmit}>
          <div className="Textbox_sign" style={{ marginTop: "15px" }}>
            <text className="Intro_sign"> </text>
          </div>
          <div className="Textbox_sign">
            <text className="Intro2_sign"></text>
          </div>
          <div className="Text_sign">
            <label className="label_id" for="name">
              {" "}
            </label>
            <div className="user_id_div">
              {/* <text className="span_id">{this.state._id}</text> */}
              {this.state._id}
            </div>
          </div>
          <div className="Text_sign">
            <label for="pass"> </label>
            <input
              type="password"
              name="pass"
              value={this.state.pass}
              onChange={this.handleChange}
              className="Input_sign"
            />
          </div>
          <div className="Text_sign">
            <label for="pass2">  </label>
            <input
              type="password"
              name="pass2"
              value={this.state.pass2}
              onChange={this.handleChange}
              className="Input_sign"
            />
            <input
              type="button"
              value=""
              onClick={this.updatePassword}
              className="Double_sign"
            />
          </div>
          <div className="Text_sign">
            <label for="nickname"> </label>
            <span style={{ marginLeft: "15px" }}>{this.state.nickname}</span>

            <input
              name="update_nickname"
              onChange={this.handleChange}
              className="Input_sign"
            />
            {/*     state         */}
            <button onClick={this.chageNickname} className="Double_sign">
              
            </button>
            {/* <input //     .
              type="button"
              value=""
              onClick={this.chageNickname}
              className="Double_sign"
            /> */}
          </div>
          <div className="Text_sign">
            <label for="email">  </label>
            <input className="Input_sign" />
            <span>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              @changwon.ac.kr&nbsp;
            </span>
          </div>
          {/* {this.state.sendEmailClick ? (
            <div className="Text_sign">
              <label for="authCheckNum"> </label>
              <input className="Input_sign" />
              <input className="Double_sign" />
            </div>
          ) : (
            <div></div> */}
          {/* )} */}
          <div>
            <button className="Btn_sign" onClick={this.onclickss}>
              
            </button>
          </div>
        </form>
      </div>
    );
  }
}
