import React, { Component } from "react";
import "./Signup.css";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

export default class Signup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      _id: "",
      pass: "",
      pass2: "",
      email: "",
      realid: "",
      sex: "",
      checked_id: false, // ID 
      checked_email: false, //   
      checked_realid: false,
      authNum: "", // 
      authCheckNum: "", //   
      sendEmailClick: false,
      waitingEmail: true,
      open1: false, // 
      open2: false, // 
      open3: false, // 
      open4: false, // 
    };
  }

  modalopen1 = (e) => {
    e.preventDefault();
    this.setState({
      open1: true,
    });
  };
  modalclose1 = (e) => {
    e.preventDefault();
    this.setState({
      open1: false,
    });
  };
  modalopen2 = (e) => {
    e.preventDefault();
    this.setState({
      open2: true,
    });
  };
  modalclose2 = (e) => {
    e.preventDefault();
    this.setState({
      open2: false,
    });
  };
  modalopen3 = (e) => {
    e.preventDefault();
    this.setState({
      open3: true,
    });
  };
  modalclose3 = (e) => {
    e.preventDefault();
    this.setState({
      open3: false,
    });
  };
  ///////
  modalopen4 = (e) => {
    e.preventDefault();
    this.setState({
      open4: true,
    });
  };
  modalclose4 = (e) => {
    e.preventDefault();
    this.setState({
      open4: false,
    });
  };

  modalopen5 = (e) => {
    e.preventDefault();
    this.setState({
      open5: true,
    });
  };
  modalclose5 = (e) => {
    e.preventDefault();
    this.setState({
      open5: false,
    });
  };

  /////

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  count30Secondd = () => {};

  sendEmail = (e) => {
    //   
    e.preventDefault();

    if (this.state.waitingEmail === true) {
      setTimeout(
        () =>
          this.setState({
            waitingEmail: true,
          }),
        10000
      );

      // waitingEamil true
      this.setState({
        sendEmailClick: true,
      });
      const email = {
        sendEmail: this.state.email,
      };
      // console.log(email);
      fetch("api/Sendmail", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(email),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json === true) {
            // alert("  .");
            this.setState({
              open2: true,
              text2: "  .",
            });
          } else {
            // alert("  .");
            this.setState({
              open2: true,
              text2:
                "  . 10    ",
              waitingEmail: false,
            });

            this.setState({
              authNum: json,
            });
          }
        });
    } else {
      //false 
      this.setState({
        text5: " 10 ",
        open5: true,
      });
    }
  };
  //  .
  authEmail = (e) => {
    e.preventDefault();
    if (this.state.authNum.toString() === this.state.authCheckNum.toString()) {
      this.setState({
        open2: true,
        text2: "",
      });
      this.setState({
        checked_email: true,
      });
    } else {
      this.setState({
        open2: true,
        text2: " ",
      });
    }
  };

  check = (re, what, message) => {
    if (re.test(what)) {
      return true;
    }
    this.setState({
      open1: true,
      text1: message,
    });
    return false;
  };

  checkId = (e) => {
    e.preventDefault();
    // var re = /^[a-zA-Z0-9]{4,12}$/; // 4~12    
    var re = /^[a-zA-z-]{2,8}$/;
    if (
      !this.check(
        re,
        this.state._id,
        " 2~8 '' ''  ."
      )
    ) {
      return false;
    } else {
      const checkId = {
        check_Id: this.state._id,
      };
      fetch("api/CheckId", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(checkId),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json) {
            this.setState({
              open1: true,
              text1: "   .",
            });
            this.setState({
              checked_id: true,
            });
          } else {
            this.setState({
              open1: true,
              text1: "   .",
            });
          }
        });
    }
  };

  //  
  checkRealid = (e) => {
    e.preventDefault();
    var re = /^[a-z]+[a-z0-9]{5,19}$/g;
    if (
      !this.check(
        re,
        this.state.realid,
        "   6~20    ."
      )
    ) {
      return false;
    } else {
      const checkRealid = {
        checkRealid: this.state.realid,
      };
      fetch("api/checkRealid", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(checkRealid),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json) {
            this.setState({
              open4: true,
              text4: "  .",
              checked_realid: true,
            });
          } else {
            this.setState({
              open4: true,
              text4: "   .",
            });
          }
        });
    }
  };

  moveLogin = (e) => {
    e.preventDefault();
    window.location.replace("/");
  };

  onSubmit = (e) => {
    e.preventDefault(); //    .
    if (!this.state.checked_realid) {
      //    
      // alert("   ");
      this.setState({
        open2: true,
        text2: "   ",
      });
    } else if (!this.state.checked_id) {
      this.setState({
        open2: true,
        text2: "   ",
      });
    } else if (!(this.state.pass === this.state.pass2)) {
      this.setState({
        open2: true,
        text2: "  .",
      });
    } else if (!this.state.checked_email) {
      this.setState({
        open2: true,
        text2: "  .",
      });
    } else if (this.state.sex === "") {
      this.setState({
        open2: true,
        text2: "  .",
      });
    } else {
      const user_info = {
        _id: this.state._id,
        pass: this.state.pass,
        pass2: this.state.pass2,
        email: this.state.email,
        realid: this.state.realid,
        sex: this.state.sex,
      };
      fetch("api/Signup", {
        method: "post",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify(user_info),
      })
        .then((res) => res.json())
        .then((json) => {
          if (json) {
            this.setState({
              open3: true,
              text3: " ",
            });
            window.location.href = "/";
          } else {
            this.setState({
              open3: true,
              text3: " ",
            });
          }
        });
    }
  };

  render() {
    return (
      <div className="White_sign">
        <Dialog
          open={this.state.open1}
          onClose={this.modalclose1}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {this.state.text1}
            </DialogContentText>
          </DialogContent>
        </Dialog>
        <Dialog
          open={this.state.open2}
          onClose={this.modalclose2}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {this.state.text2}
            </DialogContentText>
          </DialogContent>
        </Dialog>
        <Dialog
          open={this.state.open3}
          onClose={this.modalclose3}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title2">{this.state.text3}</DialogTitle>
        </Dialog>

        <Dialog
          open={this.state.open5}
          onClose={this.modalclose5}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {this.state.text5}
            </DialogContentText>
          </DialogContent>
        </Dialog>
        <Dialog
          open={this.state.open4}
          onClose={this.modalclose4}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {this.state.text4}
            </DialogContentText>
          </DialogContent>
        </Dialog>
        <form className="Container_sign" onSubmit={this.onSubmit}>
          <div className="Textbox_sign" style={{ marginTop: "15px" }}>
            <text className="Intro_sign"> </text>
          </div>
          <div className="Textbox_sign">
            <text className="Intro2_sign"></text>
          </div>
          <div className="Text_sign">
            <label for="name"> </label>
            <input
              type="text"
              id="name"
              name="realid"
              value={this.state.realid}
              onChange={this.handleChange}
              className="Input_sign"
            />
            <button className="Double_sign" onClick={this.checkRealid}>
              
            </button>
          </div>
          <div className="Text_sign">
            <label for="name"> </label>
            <input
              type="text"
              id="name"
              name="_id"
              value={this.state._id}
              onChange={this.handleChange}
              className="Input_sign"
            />
            <button className="Double_sign" onClick={this.checkId}>
              
            </button>
          </div>

          <div className="Text_sign">
            <label for="pass"> </label>
            <input
              type="password"
              id="pass"
              name="pass"
              value={this.state.pass}
              onChange={this.handleChange}
              className="Input_sign"
            />
          </div>

          <div className="Text_sign">
            <label for="pass2">  </label>
            <input
              type="password"
              id="pass2"
              name="pass2"
              value={this.state.pass2}
              onChange={this.handleChange}
              className="Input_sign"
            />
          </div>

          <div className="Text_sign2">
            <div className="sex_label">
              <label for="sex"> </label>
            </div>
            <div className="sex_div">
              <input
                type="radio"
                name="sex"
                value="M"
                onChange={this.handleChange}
                className="sex_button"
              />{" "}
              
              <input
                type="radio"
                name="sex"
                value="F"
                onChange={this.handleChange}
                className="sex_button"
              />{" "}
              
            </div>
          </div>

          <div className="Text_sign">
            <label for="email"> </label>
            <input
              type="text"
              id="email"
              name="email"
              value={this.state.email}
              onChange={this.handleChange}
              className="Input_sign_email"
            />
            <span className="email_text">@changwon.ac.kr</span>

            <button onClick={this.sendEmail} className="Double_sign">
              
            </button>
          </div>
          {this.state.sendEmailClick ? (
            <div className="Text_sign">
              <label for="authCheckNum"> </label>
              <input
                type="text"
                id="authCheckNum"
                name="authCheckNum"
                value={this.state.authCheckNum}
                onChange={this.handleChange}
                className="Input_sign"
              />
              <button onClick={this.authEmail} className="Double_sign">
                
              </button>
            </div>
          ) : (
            <div></div>
          )}

          <div className="btn_sigh">
            <button className="Btn_sign" type="submit">
              
            </button>
            <button className="Btn_sign2" onClick={this.moveLogin}>
              
            </button>
          </div>
        </form>
      </div>
    );
  }
}
